.main-footer {
  background-color: #4b453b;
  box-shadow: 0 0 5px #4b453b;
}

.main-footer__container {
  padding: 30px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1279px) {
  .main-footer__container {
    padding: 25px 24px;
  }
}
@media (max-width: 767px) {
  .main-footer__container {
    padding: 20px 16px;
  }
}

.main-footer__title {
  color: #fdfcf8;
  user-select: none;
  max-width: 200px;
  font-weight: 400;
  letter-spacing: 0.05em;
  margin: 0;
  margin-right: 0;
  font-size: 20px;
  height: fit-content;
}

.main-footer__button {
  transition: 0.3s;
  max-width: 200px;
  user-select: none;
  cursor: pointer;
  transition: 0.3s;
  width: fit-content;
  user-select: none;
  font-weight: 400;
  font-size: 18px;
  color: #fdfcf8;
  background-color: #eba675;
  border: none;
  border-radius: 5px;
  padding: 7px;
}
@media (max-width: 767px) {
  .main-footer__button {
    max-width: 80px;
    font-size: 14px;
  }
}

.main-footer__button:hover {
  transition: 0.3s;
  box-shadow: 0 0 10px #eba675;
  text-shadow: 0 0 10px #fdfcf8;
}

.main-footer__button:active {
  transition: 0.3s;
  background-color: #fdfcf8;
  color: #eba675;
  box-shadow: 0 0 10px #fdfcf8;
  text-shadow: 0 0 10px #eba675;
}

